@import '../vars.scss';

.SponsorCard {
    width: 100%;
    border-radius: 10px;
    background-color: white;
    padding: 12px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box; //чтобы брал паддинг от ширины, а не доп
    outline: none;
    cursor: pointer;

    
    &__Logo {
        margin-top: 5%;
    }
    &__Title {
        margin: 0 auto;
        font-size: 20px;
        font-weight: 600;
        padding-top: 5%;
        color: black;
    }
    &__Description {
        margin: 0 auto;
        font-size: 13px;
        padding-top: 1%;
        padding-bottom: 5%;
        color: black;
    }
    a {
        text-decoration: none;
    }
}